body {
  background-image: url(/react-project/static/media/Portland.62f04524.jpg);
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.home-header {
  text-align: center;
  font-size: 28px;
  border: solid #61dafb;
  background-color: beige;
}

.Tabs {
  padding: 1rem 1rem 3rem 1rem;
  text-align: center;
}

.f-tab {
  color: turquoise;
  padding: 1rem;
}
.cg-tab {
  color: turquoise;
  padding: 1rem;
}
.h-tab {
  color: turquoise;
  padding: 1rem;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.icon-title {
  font-style: italic;
  color: gold;
  padding: 2rem;
  text-align: center;
}

.form-titles {
  padding-top: 1rem;
}

.Headline {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: 30px;
}

.card {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  margin: 1rem 20% 0 20%;
  border-style: groove;
  background-color: white;
  font-size: 12px;
  opacity: 0.9;
}

.feild-set {
  margin: 0 20% 5% 20%;
  background-color: white;
  opacity: 0.9;
}

.myForm {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.yo {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  padding-top: 1rem;
}

.selectBox {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}

.select {
  background-color: white;
  opacity: 0.9;
}

.icon-buttons {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  background-color: white;
  width: 50px;
  margin: 1px;
  opacity: 0.9;
}

.save-button {
  background-color: lightblue;
  border: 2px solid yellow;
  border-radius: 50%;
  margin: 1rem 0 0 0;
}

.error {
  color: red;
  font-size: 12px;
  margin: 0px;
}

.radio-titles {
  margin: 8px 0 0 0;
}

.select-style {
  background: white;
  font-size: 12px;
}

input[id="type"] {
  transition: width 0.4s ease-in-out;
}

input[id="type"]:focus {
  width: 50%;
  background-color: lightblue;
}

.header {
  font-size: 24px;
  text-align: center;
  padding: 2rem;
}
.container {
  display: grid;
  width: 100%;
  height: 600px;
  grid-column-gap: 5px;
  grid-row-gap: 5px;
  -webkit-justify-content: center;
          justify-content: center;
}

.Item {
  border: double;
  padding: 1rem;
}

.color-item {
  width: 50px;
  height: 25px;
  margin: 8px;
}

.color-buttons {
  padding: 25px;
  margin: 4px;
}

.color-buttons-container {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  padding: 1rem;
}

